<template>
  <div id="app">
    <header-nav class="fixed-top"></header-nav>
    <!-- <router-view/> -->      
    <div class=" container my-content">
      <content-collect></content-collect>
    </div>
  </div>
</template>

<script>

//局部注册组件
import Header from './components/Header-nav'
import Contentcollect from './components/Content-collect'

export default {
  name: 'App',

  //局部注册组件
  components:{
    "header-nav": Header,
    "content-collect": Contentcollect,
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  background: rgb(242, 243, 244);
  height: 2000px;

}
.my-content{
  background: rgb(248, 246, 246);
  height: 2000px;
  padding-top: 100px;

}
</style>



